@charset "UTF-8";

@font-face {
  font-family: "insurecx";
  src:url("../fonts/insurecx.eot");
  src:url("../fonts/insurecx.eot?#iefix") format("embedded-opentype"),
    url("../fonts/insurecx.woff") format("woff"),
    url("../fonts/insurecx.ttf") format("truetype"),
    url("../fonts/insurecx.svg#insurecx") format("svg");
  font-weight: normal;
  font-style: normal;

}

[data-icon]:before {
  font-family: "insurecx" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="cx-"]:before,
[class*=" cx-"]:before {
  font-family: "insurecx" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

